@import url("https://fonts.googleapis.com/css2?family=Plus+Jakarta+Sans:wght@400;500;600;700;800&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Rubik:wght@500&display=swap");
@import url("https://fonts.googleapis.com/icon?family=Material+Icons");

body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --semibold-type16: "Plus Jakarta Sans";
  --font-rubik: Rubik;

  /* font sizes */
  --semibold-type16-size: 16px;
  --font-size-lg: 18px;
  --font-size-3xl: 22px;
  --font-size-29xl: 48px;
  --font-size-13xl: 32px;
  --semibold-type20-size: 20px;
  --font-size-xs: 12px;

  /* Colors */
  --white: #fff;
  --color-steelblue-100: #486284;
  --black: #1a202c;
  --color-gainsboro-100: #dee5ed;
  --color-gainsboro-200: #d3dce7;
  --color-silver: #c4c4c4;
  --color-lightsteelblue: #aab5c3;

  /* Gaps */
  --gap-5xl: 24px;
  --gap-5xs: 8px;

  /* Paddings */
  --padding-xl: 20px;

  /* border radiuses */
  --br-9xs: 4px;
  --br-3xs: 10px;
}
